import {
  createRouter,
  createWebHistory,
  createWebHashHistory,
} from "vue-router";

//路由数组
const routes = [
  {
    //基本格式
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
    children: [],
  },

  {
    path: "/home",
    name: "首页",
    component: () => import("../layout"),
    children: [
      {
        path: "/home",
        name: "首页",
        component: () => import("../views/index/index"),
      },
      {
        path: "/userCenter",
        name: "个人中心",
        component: () => import("../views/userCenter/index"),
      },

      {
        path: "/sys/role",
        name: "角色管理",
        component: () => import("../views/sys/role/index"),
      },
      {
        path: "/sys/menu",
        name: "菜单管理",
        component: () => import("../views/sys/menu/index"),
      },
      {
        path: "/config/sources",
        name: "数据源配置",
        component: () => import("../views/config/sources/index"),
      },
      {
        path: "/config/migration",
        name: "数据迁移配置",
        component: () => import("../views/config/migration/index"),
      },
      {
        path: "/config/migration/operation/:id",
        name: "数据迁移操作",
        component: () => import("../views/config/migration/operation/index"),
      },
      {
        path: "/code/list",
        name: "代码列表",
        component: () => import("../views/code/list/index"),
      },
      {
        path: "/code/run",
        name: "运行记录",
        component: () => import("../views/code/run/index"),
      },
      {
        path: "/code/run/details/:id",
        name: "运行记录详情",
        component: () => import("../views/code/run/details/index"),
      },
      {
        path: "/sys/user",
        name: "用户管理",
        component: () => import("../views/sys/user/index"),
      },
    ],
  },

  {
    path: "/",
    name: "Home1",
    redirect: "/home",
  },

  {
    path: "/:catchAll(.*)",
    name: "404",
    component: () => import("@/views/error/index.vue"),
  },
];

//路由对象
//导出路由对象，在main.js中引用
export default createRouter({
  history: createWebHistory(),
  routes, //上面的路由数组
});
